@import url(https://fonts.googleapis.com/css?family=Jura:400);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*loading button*/
#custom_loading{
left: 0px;
  margin-top: 350px;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
/*live button*/
#live_text{
  pointer-events: none;
  cursor: default;
  text-align: center;
	line-height: 50px;
	font-size: 25px;
  transition-timing-function: linear;
  background: linear-gradient(42deg, #f51111, #64f005);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 3s ease infinite;
          animation: AnimationName 3s ease infinite;
}
@-webkit-keyframes AnimationName {
  0%{background-position:0% 52%}
  50%{background-position:100% 49%}
  100%{background-position:0% 52%}
}
@keyframes AnimationName {
  0%{background-position:0% 52%}
  50%{background-position:100% 49%}
  100%{background-position:0% 52%}
}

#search_bar{
  margin: auto;
  width: 50%;
  border: 4px solid;
  border-color:linear-gradient(to right, #00d2ff 0%, #3a7bd5 51%, #00d2ff 100%);
  padding: 10px;
}
body{
  grid-template-columns: 1fr minmax(200px,400px) 1fr;
  grid-template-rows: 1fr minmax(auto,1fr) 1fr;
  width: 100%;
  height: 100vh;
  background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
          animation: Gradient 15s ease infinite;

}

@media only screen and (max-width: 600px) {
 #root{
   width:1000px
 }
 .pagination{
   margin-top: 100px;
 }
}
@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}
@keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}
